import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  Select,
  message
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {CsxSelect} from 'Form'
import {API_BUSINESSTYPEVALUES, API_WMSORDERTYPEKEYVALUES} from 'api'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.waveRule}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
    // 改变当前的输入值
    onInputChange(values) {
      const {searchParams, update} = this.props
      update({searchParams: {...searchParams, ...values}})
    }
  // 新增
  handleAdd() {
    const {handleAddOrEdit, searchParams, handleGetList, autoExecuteTime} = this.props
    console.log(searchParams)
    if(searchParams.businessType === undefined) {
      message.success('请选择出库类型、业务类型')
    }
    if(searchParams.businessType === '' && searchParams.autoExecuteTime === undefined) {
      message.success('请选择业务类型、自动执行时间')
    } else {
      if(searchParams.autoExecuteTime===undefined) {
        message.success('请选择自动执行时间')
      } else {
        let n = searchParams.autoExecuteTime +':00'
        handleAddOrEdit({data: {
          businessTypeCode: searchParams.businessType.value,
          autoExecuteTime: n
        }}, ()=> {
          message.success('添加成功')
          handleGetList()
        })
      }
    }
  }

  // 重置
  reset = () => {
  }
  render() {
    const {
      searchParams: {
        wmsOrderType,
        businessType,
        autoExecuteTime
      },
      selectData,
    } = this.props
    selectData.map((i)=>{
      selectData[i]<10 ? selectData[i] = '0'+ selectData[i] : selectData[i]
    })
    let selectMeun = selectData.map((o ,i) => <Select.Option value={o} key={o}>{o}:00</Select.Option>)
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={23} offset={1}>
            <h3 style={{fontWeight: "bold", paddingLeft: 6}}>分配全量满足配置表</h3>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <CsxSelect style={{width: 163}}
                label="出库类型"
                dateApi={API_WMSORDERTYPEKEYVALUES}
                selectItem={{data: wmsOrderType, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  const {searchParams, update} = this.props
                  update({searchParams: {...searchParams, wmsOrderType: value, businessType: ''}})
                }}
              />
            </div>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <CsxSelect style={{width: 163}}
                label="业务类型"
                dateApi={API_BUSINESSTYPEVALUES}
                searchParams={{wmsOrderType: wmsOrderType && wmsOrderType.value}}
                selectItem={{data: businessType, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  this.onInputChange({businessType: value})
                }}
              />  
            </div>
          </Col>
          <Col span={8} offset={1}>
            <div className='commonInput'>
              <label>自动执行时间：</label>
              <Select 
                style={{width: 163}}
                placeholder = "请选择"
                onSelect = {(value, e) =>{
                  this.onInputChange({autoExecuteTime: value})
                }}
              >
                {selectMeun}
              </Select>
            </div>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary"  onClick={() => {
                  this.handleAdd()}}>添加</Button>
            </Row>
          </Col>
        </Row>
      </Form>
      )
    }
}

export default Form.create()(App)



