import * as actionType from './actionType'
import {getData} from 'util'
import {API_WAVERULES_PAGE, API_CREATEWAVERULES_CREATE, API_DELETEWAVERULES_DELETE} from 'api'
//同步
export const update = (object) => ({
  type: actionType.WAVERULE_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.WAVERULE_UPDATE, data: {tableLoading: true}})
  getData({api: API_WAVERULES_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.WAVERULE_UPDATE,
      data: {
        tableLoading: false,
        searchResult: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.WAVERULE_FILE
    })
  })
}

// 创建分配规则
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.WAVERULE_UPDATE, data: {modalTuttonLoading: true}})
  let api = API_CREATEWAVERULES_CREATE
  getData({api, ...reqData}).then(data => {

    // 相应的逻辑判断
    dispatch({
      type: actionType.WAVERULE_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.WAVERULE_FILE
    })
  })
}

// 删除
export const handleDel = (reqData, callBack) => (dispatch) => {
  getData({api: API_DELETEWAVERULES_DELETE, ...reqData}).then(data => {
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.WAVERULE_FILE
    })
  })
}


